
@font-face {
  font-family: old;
  src: url(Segment7Standard.otf);
}
@font-face {
  font-family: num;
  src: url(conthrax-sb.otf);
}

body {
  margin: 20px;
  padding: 0;
  background: rgb(231, 246, 255);
}

.Calc {
 
  
  margin: auto;
  width: 500px;
  height: 580px;
  background: rgb(255, 248, 170);
  border-radius: 45px;
  padding: 20px;
  justify-content: center;
  box-shadow: 15px 15px  30px rgba(57, 5, 70,0.5), inset 10px 10px  10px   rgba(255, 255, 255, 0.8),  inset -12px -12px  12px rgba(85, 50, 5, 0.4) ;
  user-select: none;
}

.TextOutput {

  position:absolute;
  width: 98%;
  height: 86%;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 20px;
 text-align: right;
 font-family: old ;
 color: rgb(41, 3, 39);
 text-shadow: 5px 5px 18px rgba(75, 39, 255, 0.4), -5px -5px 18px rgba(75, 39, 255, 0.4);
 box-sizing: border-box;
  padding-top: 12px;
  overflow: hidden;
  word-wrap: break-word;
  user-select:all;
}

.nullOut {

  color: rgba(8, 17, 86, 0.1);
  text-shadow: 2px 2px 12px rgba(187, 112, 7, 0.4);
  background: linear-gradient(90deg,rgb(97, 194, 100),rgb(161, 211, 111));
  box-shadow:  inset -8px -8px  20px rgb(226, 255, 123,0.5),   inset  7px 7px  12px rgb(15, 54, 2,0.6);  

}
.outField {
  box-sizing: border-box;
  border-radius: 25px ;
  padding: 5.5px;
  display: flex;
  justify-content: center;
  background: rgb(255, 248, 170);
  width:100%;
  height: 13%;
  position: relative;
  margin-bottom: 20px;
  font-size:300%;
  box-shadow: 5px 5px  12px rgba(70, 41, 5, 0.5),  -5px -5px  12px rgb(255, 255, 255) ;
}
.ButtonNum {

  width:70px;
  height: 70px;
  border-radius: 80px;

  padding-top: 23% ;

  box-sizing: border-box;
  
  justify-content: center;
  text-align: center;
  margin-left: 10px;
  margin-bottom: 10px;
  
}

.ButtonNum:active {
  transition: 0.37s;
  box-shadow: 0px 0px  0px rgba(70, 41, 5, 0.8),  -0px -0px  0px rgba(255, 255, 255,0) , inset 7px 7px  15px rgb(147, 109, 4,0.87),   inset  -7px -7px  12px rgb(147, 109, 4,1);  
  background: rgb(233, 226, 151);
}
.colorNum {
  background: rgb(26, 255, 0);
  box-shadow: 5px 5px  12px rgba(70, 41, 5, 0.8),  -7px -7px  12px rgba(255, 255, 255,0) , inset 7px 7px  15px rgb(255, 255, 222),   inset  -7px -7px  12px rgb(147, 109, 4,0.87);  
  background: rgb(255, 248, 170);
}

.colorOper {
    background: rgb(255, 178, 11);
   box-shadow: 5px 5px  8px rgba(70, 41, 5, 0.8),  -7px -7px  12px rgba(255, 255, 255,0) , inset 7px 7px  15px rgb(255, 230, 162),   inset  -7px -7px  12px rgba(108, 41, 0, 0.8);  
}
.colorOper:active {
  transition: 0.37s;
  background: rgb(226, 156, 7);
  box-shadow: 0px 0px  0px rgba(70, 41, 5, 0.8),  -0px -0px  0px rgba(255, 255, 255,0) , inset 7px 7px  15px rgba(108, 41, 0, 0.87),   inset  -7px -7px  12px rgba(108, 41, 0, 1);   
}
.ButtonNumHor {
  width:174px;
  height: 70px;
  border-radius: 80px;
  padding-top: 10% ;
  
  box-sizing: border-box;
 
  justify-content: center;
  text-align: center;
  grid-column: span 2;
  margin-left: 10px;
  margin-bottom: 10px;
}

.ButtonNumVer {
  width:70px;
  height: 174px;
  border-radius: 80px;
  padding-top: 80% ;
 
  box-sizing: border-box;

  justify-content: center;
  text-align: center;
  grid-row: span 2;
  margin-left: 10px;
  margin-bottom: 10px;
}
.buttons {
  font-size: 180%;
  font-family: num ;
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 16px;
}



